


















import Vue from "vue";
import Component from 'vue-class-component';

@Component({
    computed:{
        tags(){
            return this.$store.state.tagsList.tagsList
        }
    }
})

export default class Tag extends Vue {

    created(){
        this.$store.commit("init")
    }
    createTag(){
        let name = window.prompt("请输入标签名");
        
        this.$store.commit("create",name)
        
    }
}
