























import Vue from 'vue';
import  Component  from 'vue-class-component';

let NumberProps = Vue.extend({
    props:{
        amount:String
    }
})

@Component

export default class NumberPad extends NumberProps{
    get output(){
        
        return this.amount;
    }
    set output(val){
        this.$emit("update:amount",val)
    }
    inputNumber(event: MouseEvent){
        let clickBtn = (event.target as HTMLButtonElement)
        let inputNum = clickBtn.textContent!;
        let deleteBtn = document.querySelector(".deleteBtn");
        let emptyBtn = document.querySelector(".emptyBtn");
        let okBtn = document.querySelector(".okBtn");

        if(clickBtn === deleteBtn){
            
            if(this.output.length === 1){
                this.output = "0"
            } else {
                this.output = this.output.slice(0,-1)
            }
            
            return;
        }
        if(clickBtn === emptyBtn){
            this.output = '0'
            return 
        }
        if(this.output.length >= 16){
            alert("超出上限")
            return;
        }
        
        if(this.output === "0"){
            if('0123456789'.indexOf(inputNum)>=0){
                this.output = inputNum;
                return 
            }
        }
        if(this.output.indexOf(".")>=0 && inputNum === "."){
            return 
        }
        
        
        if(clickBtn === okBtn){
            if(this.output[this.output.length-1]==="."){
               this.output = this.output.slice(0,-1);
         
            }
           
            this.$emit("submit");
            
            return ;
        }
        this.output += inputNum;
        
    }
}

