











import Vue from "vue";
import Component from "vue-class-component";

let NotesProps = Vue.extend({
    props:{
        notes:String,
        notesName:String,
        placeholder:String
    }
})

@Component<Notes>({
    computed:{
        value:{
            get(){
                return this.notes;
            },
            set(newValue){
                this.$emit("update:notes",newValue)
            }
        }
    }
})

export default class Notes extends NotesProps {

}
